rwards;\n  -webkit-animation-fill-mode: forwards;\n}\n\n.sideburn {\n  width: 8px;\n  height: 25px;\n  background: #8a6634;\n  position: absolute;\n  bottom: -25px;\n  opacity: 0;\n  animation: sideburn-anim 0.7s 0.9s 1 ease;\n  animation-fill-mode: forwards;\n  -webkit-animation: sideburn-anim 0.7s 0.9s 1 ease;\n  -webkit-animation-fill-mode: forwards;\n}\n\n.sideburn#left {\n  left: 12px;\n}\n\n.sideburn#right {\n  right: 12px;\n}\n\n.face {\n  width: 180px;\n  height: 0px;\n  border-radius: 48px 48px 0 0;\n  background: #ffe4be;\n  position: absolute;\n  top: 40px;\n  left: 8px;\n  animation: hair-anim 0.1s 0.8s 1 linear;\n  animation-fill-mode: forwards;\n  -webkit-animation: hair-anim 0.1s 0.8s 1 linear;\n  -webkit-animation-fill-mode: forwards;\n  z-index: 3;\n}\n.nose {\n  width: 20px;\n  height: 45px;\n  opacity: 1;\n  background: #ffe4be;\n  border-top-left-radius: 20px;\n  position: absolute;\n  left: 50%;\n  top: 80px;\n  margin-left: -20px;\n  animation: shadow-anim 0.7s 3s 1 ease;\n  animation-fill-mode: forwards;\n  -webkit-animation: shadow-anim 0.7s 3s 1 ease;\n  -webkit-animation-fill-mode: forwards;\n  opacity: 0;\n  z-index: 5;\n}\n.ear {\n  width: 24px;\n  height: 35px;\n  background: #ffe4be;\n  border-radius: 12px;\n  position: absolute;\n  top: 116px;\n  animation: grow 0.7s 1.3s 1 ease;\n  animation-fill-mode: forwards;\n  -webkit-animation: grow 0.7s 1.3s 1 ease;\n  -webkit-animation-fill-mode: forwards;\n  -webkit-transform: scale(0);\n  transform: scale(0);\n}\n.mouth {\n  width: 66px;\n  height: 33px;\n  border-radius: 0 0 33px 33px;\n  background: white;\n  position: absolute;\n  top: 150px;\n  left: 50%;\n  margin-left: -33px;\n  animation: grow 0.7s 2.6s 1 ease;\n  animation-fill-mode: forwards;\n  -webkit-animation: grow 0.7s 2.6s 1 ease;\n  -webkit-animation-fill-mode: forwards;\n  -webkit-transform: scale(0);\n  transform: scale(0);\n}\n.ear#left {\n  left: -20px;\n}\n.ear#right {\n  right: -20px;\n}\n.eye-shadow {\n  width: 30px;\n